import React, { useEffect, useState } from "react";

import "./Projects.css";

import Axios from "axios";

import ProjectCard from "../../components/ProjectCard/ProjectCard";
import Button from "../../components/Button/Button";

const Projects = () => {
  const [repos, setRepos] = useState([]);
  useEffect(() => {
    Axios.get(
      "https://api.github.com/users/KarthikeyanRanasthala/repos?per_page=100"
    )
      .then(resp => {
        let arr = [
          {
            nameWithOwner: "Online Test Platform",
            description: "A Platform To Bring Teacher & Students Together",
            forkCount: "Private",
            stargazers: {
              totalCount: "Private"
            },
            id: 1,
            diskUsage: 0,
            builtWith: ["React", "Redux", "Bootstrap", "Firebase"],
            demoUrl: "https://onlinetestplatform.firebaseapp.com/"
          }
        ];

        for (let i = 0; i < resp.data.length; i++) {
          let description = "";
          let builtWith = [];
          let nameWithOwner = "";
          let id = 0;
          let tag = "";
          let demoUrl = "";
          let packageUrl = "";
          if (resp.data[i].name === "fake-authentication") {
            nameWithOwner = "Fake Authentication";
            description = "A Fake Authentication System With LocalStorage";
            builtWith = ["JavaScript"];
            id = 2;
            tag = "Available On NPM";
            packageUrl = "https://www.npmjs.com/package/fake-authentication";
          } else if (resp.data[i].name === "react-unit-converter") {
            nameWithOwner = "React Unit Converter";
            description = "185 Different Measurement Units";
            builtWith = ["React", "Material UI"];
            id = 3;
            demoUrl = "https://unit-converter.karthikeyan.space/";
          } else if (resp.data[i].name === "react-yts-movies") {
            nameWithOwner = "YTS Movies";
            description = "A Better Way To Access YTS (YIFY) Movies";
            builtWith = ["React", "Materialize CSS"];
            id = 4;
            demoUrl = "https://yts.karthikeyan.space/";
          } else if (resp.data[i].name === "masai-week-7") {
            nameWithOwner = "Zomato Clone";
            description = "Zomato.com Landing Page Clone";
            builtWith = ["React"];
            id = 5;
            demoUrl = "https://karthikeyanranasthala.github.io/masai-week-7/";
          } else if (resp.data[i].name === "masai-week-3") {
            nameWithOwner = "Chess";
            description = "A Simple Chess Game";
            builtWith = ["HTML", "CSS", "JavaScript"];
            id = 6;
            demoUrl = "https://karthikeyanranasthala.github.io/masai-week-3";
          } else if (resp.data[i].name === "opensource-birthday") {
            nameWithOwner = "OpenSource Birthday";
            description = "Find Your OpenSource Birthday using Github API";
            builtWith = ["React"];
            id = 7;
            demoUrl = "https://opensource-birthday.netlify.com/";
          } else if (resp.data[i].name === "masai-week-5") {
            nameWithOwner = "CryptoGecko";
            description = "One Stop For All Your CryptoCurrency Statistics";
            builtWith = ["HTML", "CSS", "JavaScript"];
            id = 8;
            demoUrl = "https://karthikeyanranasthala.github.io/masai-week-5/";
          } else {
            continue;
          }
          arr.push({
            nameWithOwner: nameWithOwner,
            description: description,
            forkCount: resp.data[i].forks_count,
            stargazers: {
              totalCount: resp.data[i].stargazers_count
            },
            url: resp.data[i].html_url,
            id: id,
            diskUsage: resp.data[i].size,
            builtWith: builtWith,
            tag: tag,
            demoUrl: demoUrl,
            packageUrl: packageUrl
          });
        }
        const sortedArr = arr.sort((a, b) => a.id - b.id);
        setRepos(sortedArr);
      })
      .catch(err => console.log(err));
  }, []);

  return (
    <div className="main" id="Projects">
      <h1 className="project-title">Projects</h1>
      <div className="repo-cards-div-main">
        {repos.length !== 0 ? (
          repos.map((ele, i) => <ProjectCard repo={ele} key={i} />)
        ) : (
          <p>Loading...</p>
        )}
      </div>
      <Button
        text={"More Projects"}
        className="project-button"
        href="https://github.com/karthikeyanranasthala"
        newTab={true}
      />
    </div>
  );
};

export default Projects;
