import React, { useEffect, useState } from "react";

import Axios from "axios";

import "./Calendar.css";

import GitHubCalendar from "react-github-calendar";
import ReactTooltip from "react-tooltip";

const Calendar = () => {
  const [streakData, setStreakData] = useState("");
  useEffect(() => {
    Axios.get("https://github-streak.herokuapp.com/karthikeyanranasthala")
      .then(resp => {
        // console.log(resp);
        setStreakData(resp.data);
      })
      .catch(err => console.log(err));
  }, []);
  return (
    <>
      <div id="calendar">
        <GitHubCalendar username="karthikeyanranasthala" color="#55198b">
          <ReactTooltip delayShow={50} html />
        </GitHubCalendar>
      </div>
      {streakData ? (
        <div id="streak-div">
          <p className="streaks">
            Longest Streak: {streakData.longestStreak.days} Days
          </p>
          <p className="streaks">
            Current Streak: {streakData.currentStreak.days} Days
          </p>
        </div>
      ) : (
        <></>
      )}
    </>
  );
};

export default Calendar;
